import useOry from "hooks/useOry";
import ErrorPage from "pages/error";
import React, { ComponentType } from "react";
import { Redirect, Route, RouteComponentProps } from "react-router-dom";
import { keto } from "services/keto";
import { kratos } from "services/kratos";

const PrivateRoute = (props: {
  component: ComponentType<RouteComponentProps>;
  loaderComponent: ComponentType;
  routeRedirect: string;
  path: string;
}) => {
  const [loading, error, session] = useOry(kratos, keto);
  console.log(loading, session);
  const {
    component: Component,
    loaderComponent: LoaderComponent,
    routeRedirect,
    path,
    ...rest
  } = props;

  return (
    <React.Fragment>
      {loading && <LoaderComponent />}
      {!loading &&
        (session.active ? (
          <Route
            {...rest}
            path={path}
            exact
            render={(props): any => {
              return error ? <ErrorPage id={403} /> : <Component {...props} />;
            }}
          />
        ) : (
          <Redirect
            to={{
              pathname: routeRedirect,
            }}
          />
        ))}
    </React.Fragment>
  );
};

export default PrivateRoute;
