import "App.css";

import { rules } from "services/abac";
import React, { lazy } from "react";
import { AbacProvider } from "react-abac";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import { useRecoilValue } from "recoil";

import { SESSION_STATE } from "store";
import { Suspense } from "react";
import Loader from "components/Loader";
import PrivateRoute from "components/PrivateRoute";
import { kratos } from "services/kratos";
const PdfViewer = lazy(() => import("pages/pdf"));
const Login = lazy(() => import("pages/login"));
const Dashboard = lazy(() => import("pages/dashboard"));
const Register = lazy(() => import("pages/register"));
const Landing = lazy(() => import("pages/landing"));
const Course = lazy(() => import("containers/Course"));
const Search = lazy(() => import("pages/search"));
const Recover = lazy(() => import("pages/recoveryn"));
const Settings = lazy(() => import("pages/settings"));

const App = () => {
  const { role } = useRecoilValue(SESSION_STATE);
  kratos.whoami(undefined, undefined, { withCredentials: true }).then().catch();
  return (
    <AbacProvider rules={rules} roles={[role]}>
      <BrowserRouter basename={"/courses"}>
        <Switch>
          <Suspense fallback={<Loader />}>
            <Route path="/test/cpanel" component={Dashboard} />
            <PrivateRoute
              path="/cpanel"
              component={Dashboard}
              loaderComponent={Loader}
              routeRedirect="/login"
            />
            <PrivateRoute
              path="/course/:cid"
              component={Course}
              loaderComponent={Loader}
              routeRedirect="/login"
            />
            <PrivateRoute
              path="/search"
              component={Search}
              loaderComponent={Loader}
              routeRedirect="/login"
            />
            <PrivateRoute
              path="/files/:fid/:fname"
              component={PdfViewer}
              loaderComponent={Loader}
              routeRedirect="/login"
            />
            <Route path="/login" component={Login} />
            <Route path="/registration" component={Register} />
            <Route path="/recovery" component={Recover} />
            <Route path="/settings" component={Settings} />
            <Route path="/" exact component={Landing} />
          </Suspense>
        </Switch>
      </BrowserRouter>
    </AbacProvider>
  );
};

export default App;
